import React, { useState, useRef, useEffect } from 'react';
import { Button, Fade, Slide, Grow } from '@mui/material';
import './BeadRating.css';

const BeadRating = () => {
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [showResult, setShowResult] = useState(false);
  const [currentMessage, setCurrentMessage] = useState('');
  const [fullMessage, setFullMessage] = useState('');
  const [scores, setScores] = useState({
    love: 0,
    windfall: 0,
    regularIncome: 0,
    career: 0,
    health: 0
  });
  const [savedDesign, setSavedDesign] = useState(null);
  const [isExiting, setIsExiting] = useState(false);
  const [isGlobalLoading, setIsGlobalLoading] = useState(true);
  
  // 
  const [showShareMenu, setShowShareMenu] = useState(false);
  const [shareMessage, setShareMessage] = useState('');

  // 
  const psychicAdvice = ``;

  // 
  useEffect(() => {
    const savedDesignData = localStorage.getItem('savedBeadDesign');
    if (savedDesignData) {
      try {
        const design = JSON.parse(savedDesignData);
        setSavedDesign(design);
        console.log(':', design);
      } catch (error) {
        console.error(':', error);
      }
    }
    
    //  loading 
    const timer = setTimeout(() => {
      setIsGlobalLoading(false);
    }, 2000); // 2 loading
    
    return () => clearTimeout(timer);
  }, []);

  // 
  const getBeadDualCategoryAndScore = (beadName) => {
    // 
    const beadCategories = {
      // 
      '': { 
        primary: { category: 'love', name: '', score: 1, description: '' },
        secondary: { category: 'health', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'windfall', name: '', score: 1, description: '' },
        secondary: { category: 'career', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'regularIncome', name: '', score: 1, description: '' },
        secondary: { category: 'health', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'career', name: '', score: 1, description: '' },
        secondary: { category: 'windfall', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'health', name: '', score: 1, description: '' },
        secondary: { category: 'love', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'love', name: '', score: 1, description: '' },
        secondary: { category: 'career', name: '', score: 1, description: '' }
      },
      
      // 
      '': { 
        primary: { category: 'windfall', name: '', score: 1, description: '' },
        secondary: { category: 'health', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'regularIncome', name: '', score: 1, description: '' },
        secondary: { category: 'love', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'career', name: '', score: 1, description: '' },
        secondary: { category: 'regularIncome', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'health', name: '', score: 1, description: '' },
        secondary: { category: 'windfall', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'love', name: '', score: 1, description: '' },
        secondary: { category: 'regularIncome', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'windfall', name: '', score: 1, description: '' },
        secondary: { category: 'career', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'regularIncome', name: '', score: 1, description: '' },
        secondary: { category: 'health', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'career', name: '', score: 1, description: '' },
        secondary: { category: 'love', name: '', score: 1, description: '' }
      },
      '': { 
        primary: { category: 'health', name: '', score: 1, description: '' },
        secondary: { category: 'windfall', name: '', score: 1, description: '' }
      }
    };
    
    return beadCategories[beadName] || { 
      primary: { category: 'unknown', name: '', score: 1, description: '' },
      secondary: { category: 'unknown', score: 1, description: '' }
    };
  };

  // 
  const analyzeBeadDesign = (design) => {
    const { beads } = design;
    let scores = {
      love: 4,        // 1 + 3 = 4
      windfall: 1,    // 1
      regularIncome: 1, // 1
      career: 1,      // 1
      health: 1       // 1
    };

    console.log('', beads.length, '');
    console.log('1 + 3:', scores);

    //  - 
    beads.forEach((bead, index) => {
      const { id, name } = bead;
      
      console.log(`${index + 1}: ID=${id}, =${name}`);
      
      // ID
      const beadInfo = getBeadDualCategoryAndScore(name);
      console.log(`${index + 1}:`, { 
        id, 
        name, 
        primary: beadInfo.primary, 
        secondary: beadInfo.secondary 
      });
      
      //  - 1
      switch (beadInfo.primary.category) {
        case 'love':
          scores.love = Math.min(10, scores.love + 1);
          console.log(': +1, :', scores.love);
          break;
          
        case 'windfall':
          scores.windfall = Math.min(10, scores.windfall + 1);
          console.log(': +1, :', scores.windfall);
          break;
          
        case 'regularIncome':
          scores.regularIncome = Math.min(10, scores.regularIncome + 1);
          console.log(': +1, :', scores.regularIncome);
          break;
          
        case 'career':
          scores.career = Math.min(10, scores.career + 1);
          console.log(': +1, :', scores.career);
          break;
          
        case 'health':
          scores.health = Math.min(10, scores.health + 1);
          console.log(': +1, :', scores.health);
          break;
          
        default:
          console.log(':', beadInfo.primary);
          break;
      }
      
      //  - 1
      switch (beadInfo.secondary.category) {
        case 'love':
          scores.love = Math.min(10, scores.love + 1);
          console.log(': +1, :', scores.love);
          break;
          
        case 'windfall':
          scores.windfall = Math.min(10, scores.windfall + 1);
          console.log(': +1, :', scores.windfall);
          break;
          
        case 'regularIncome':
          scores.regularIncome = Math.min(10, scores.regularIncome + 1);
          console.log(': +1, :', scores.regularIncome);
          break;
          
        case 'career':
          scores.career = Math.min(10, scores.career + 1);
          console.log(': +1, :', scores.career);
          break;
          
        case 'health':
          scores.health = Math.min(10, scores.health + 1);
          console.log(': +1, :', scores.health);
          break;
          
        default:
          console.log(':', beadInfo.secondary);
          break;
      }
      
      console.log(`${index + 1} - :+1, :+1`);
      console.log(':', scores);
    });
    
    console.log(':', scores);
    return scores;
  };

  // 
  const generatePersonalizedAdvice = (design, scores) => {
    const { beads } = design;
    let advice = '';
    
    // 
    if (scores.love >= 8) {
      advice += '';
    } else if (scores.love >= 6) {
      advice += '';
    } else if (scores.love >= 4) {
      advice += '';
    }
    
    if (scores.windfall >= 8) {
      advice += '';
    } else if (scores.windfall >= 6) {
      advice += '';
    } else if (scores.windfall >= 4) {
      advice += '';
    }
    
    if (scores.regularIncome >= 8) {
      advice += '';
    } else if (scores.regularIncome >= 6) {
      advice += '';
    } else if (scores.regularIncome >= 4) {
      advice += '';
    }
    
    if (scores.career >= 8) {
      advice += '';
    } else if (scores.career >= 6) {
      advice += '';
    } else if (scores.career >= 4) {
      advice += '';
    }
    
    if (scores.health >= 8) {
      advice += '';
    } else if (scores.health >= 6) {
      advice += '';
    } else if (scores.health >= 4) {
      advice += '';
    }
    
    // 
    const beadNames = beads.map(bead => bead.name);
    
    // 
    const hasPink = beadNames.some(name => 
      name.includes('') && !name.includes('') && !name.includes('')
    );
    const hasPurple = beadNames.some(name => 
      name.includes('') || name.includes('') || name.includes('')
    );
    const hasYellow = beadNames.some(name => 
      name.includes('') || (name.includes('') && !name.includes(''))
    );
    const hasGreen = beadNames.some(name => 
      name.includes('') || name.includes('') || name.includes('')
    );
    const hasBlue = beadNames.some(name => 
      name.includes('') || name.includes('') || name.includes('') || name.includes('') || name.includes('')
    );
    const hasRed = beadNames.some(name => 
      name.includes('') || name.includes('') || name.includes('')
    );
    const hasOrange = beadNames.some(name => 
      name.includes('')
    );
    const hasBrown = beadNames.some(name => 
      (name.includes('') || name.includes('') || name.includes('')) && !name.includes('') && !name.includes('')
    );
    const hasWhite = beadNames.some(name => 
      name.includes('') && !name.includes('')
    );
    const hasBlack = beadNames.some(name => 
      name.includes('')
    );
    const hasSilver = beadNames.some(name => 
      name.includes('')
    );
    const hasGold = beadNames.some(name => 
      name.includes('') && !name.includes('')
    );
    
    // 
    console.log(':', beadNames);
    console.log(':', {
      hasPink, hasPurple, hasYellow, hasGreen, hasBlue, 
      hasRed, hasOrange, hasBrown, hasWhite, hasBlack, hasSilver, hasGold
    });
    
    // 
    // 
    
    if (hasPink && hasPurple) {
      advice += '';
    } else if (hasPink) {
      advice += '';
    }
    
    if (hasPurple && hasGreen) {
      advice += '';
    } else if (hasPurple) {
      advice += '';
    }
    
    if (hasYellow && hasGreen) {
      advice += '';
    } else if (hasYellow) {
      advice += '';
    }
    
    if (hasRed && hasBlue) {
      advice += '';
    } else if (hasRed) {
      advice += '';
    }
    
    if (hasOrange && hasBlue) {
      advice += '';
    } else if (hasOrange) {
      advice += '';
    }
    
    // 
    if (hasGreen) {
      advice += '';
    }
    
    if (hasBlue) {
      advice += '';
    }
    
    if (hasBrown) {
      advice += '';
    }
    
    if (hasWhite) {
      advice += '';
    }
    
    if (hasBlack) {
      advice += '';
    }
    
    if (hasSilver) {
      advice += '';
    }
    
    if (hasGold) {
      advice += '';
    }
    
         // 5
    
    // 
    const beadCount = beads.length;
    if (beadCount >= 15) {
      advice += '';
    } else if (beadCount >= 8) {
      advice += '';
    } else if (beadCount >= 5) {
      advice += '';
    } else {
      advice += '';
    }
    
                                                     // 
       
        advice += '';
        
        // 5
       
       // 
       if (scores.love > 5) {
         if (scores.love >= 8) {
           advice += '';
         } else if (scores.love >= 6) {
           advice += '';
         }
       }
       
       // 
       if (scores.windfall > 5) {
         if (scores.windfall >= 8) {
           advice += '';
         } else if (scores.windfall >= 6) {
           advice += '';
         }
       }
       
       // 
       if (scores.regularIncome > 5) {
         if (scores.regularIncome >= 8) {
           advice += '';
         } else if (scores.regularIncome >= 6) {
           advice += '';
         }
       }
       
       // 
       if (scores.career > 5) {
         if (scores.career >= 8) {
           advice += '';
         } else if (scores.career >= 6) {
           advice += '';
         }
       }
       
       // 
       if (scores.health > 5) {
         if (scores.health >= 8) {
           advice += '';
         } else if (scores.health >= 6) {
           advice += '';
         }
       }
      
             //  - 
       const totalScore = Object.values(scores).reduce((sum, score) => sum + score, 0);
       const averageScore = totalScore / Object.keys(scores).length;
       const maxScore = Math.max(...Object.values(scores));
       const minScore = Math.min(...Object.values(scores));
       const scoreRange = maxScore - minScore;
       
       // 
       const hasWarmColors = beadNames.some(name => name.includes('') || name.includes('') || name.includes('') || name.includes(''));
       const hasCoolColors = beadNames.some(name => name.includes('') || name.includes('') || name.includes(''));
       const hasNeutralColors = beadNames.some(name => name.includes('') || name.includes('') || name.includes('') || name.includes(''));
      
      // 
      if (scoreRange <= 2 && averageScore >= 7) {
        // 
        if (hasWarmColors && hasCoolColors) {
          advice += '';
        } else if (hasWarmColors) {
          advice += '';
        } else if (hasCoolColors) {
          advice += '';
        } else {
          advice += '';
        }
      } else if (maxScore >= 8 && scoreRange > 3) {
        // 
        if (hasWarmColors) {
          advice += '';
        } else if (hasCoolColors) {
          advice += '';
        } else {
          advice += '';
        }
      } else if (averageScore >= 6 && scoreRange <= 3) {
        // 
        if (hasWarmColors && hasCoolColors) {
          advice += '';
        } else if (hasWarmColors) {
          advice += '';
        } else if (hasCoolColors) {
          advice += '';
        } else {
          advice += '';
        }
      } else if (averageScore < 5) {
        // 
        if (hasWarmColors) {
          advice += '';
        } else if (hasCoolColors) {
          advice += '';
        } else {
          advice += '';
        }
      }
      
             // 
       if (beadCount >= 15) {
         advice += '';
       } else if (beadCount >= 10) {
         advice += '';
       } else if (beadCount >= 6) {
         advice += '';
       } else {
         advice += '';
       }
      
      //  - 
      if (hasWarmColors && hasCoolColors) {
        advice += '';
      } else if (hasWarmColors) {
        advice += '';
      } else if (hasCoolColors) {
        advice += '';
      } else {
        advice += '';
      }
    
    return advice;
  };

  // AI
  const startAnalysis = () => {
    if (!savedDesign) {
      alert('');
      return;
    }

    console.log(':', savedDesign); // 

    setIsAnalyzing(true);
    setCurrentMessage('');
    setShowResult(false);

    // 
    setTimeout(() => {
      // 
      const newScores = analyzeBeadDesign(savedDesign);
      console.log(':', newScores); // 
      
      // 
      setScores(newScores);

      // 
      const personalizedAdvice = generatePersonalizedAdvice(savedDesign, newScores);
      setFullMessage(personalizedAdvice);

      // 
      let index = 0;
      const typeWriter = () => {
        if (index < personalizedAdvice.length) {
          setCurrentMessage(personalizedAdvice.substring(0, index + 1));
          index++;
          setTimeout(typeWriter, 50);
        } else {
          setShowResult(true);
          setIsAnalyzing(false);
          console.log(':', newScores); // 
        }
      };
      typeWriter();
    }, 2000);
  };

  // 
  const clearSavedDesign = () => {
    localStorage.removeItem('savedBeadDesign');
    setSavedDesign(null);
    setShowResult(false);
    setCurrentMessage('');
    setFullMessage('');
  };

  // 
  const goToBeadCabinet = () => {
    window.location.href = '/';
  };

  // 
  const goHome = () => {
    setIsExiting(true);
    // 
    setTimeout(() => {
      window.location.href = '/home';
    }, 800); // 800ms 
  };

  // 
  const captureScreenshot = () => {
    try {
      // 
      if (navigator.mediaDevices && navigator.mediaDevices.getDisplayMedia) {
        navigator.mediaDevices.getDisplayMedia({ 
          preferCurrentTab: true,
          video: {
            mediaSource: 'screen'
          }
        }).then(stream => {
          const track = stream.getVideoTracks()[0];
          const imageCapture = new ImageCapture(track);
          imageCapture.grabFrame().then(imageBitmap => {
            const canvas = document.createElement('canvas');
            canvas.width = imageBitmap.width;
            canvas.height = imageBitmap.height;
            const ctx = canvas.getContext('2d');
            ctx.drawImage(imageBitmap, 0, 0);
            
            // 
            const link = document.createElement('a');
            link.download = `_${savedDesign?.designName || ''}_${new Date().toISOString().split('T')[0]}.png`;
            link.href = canvas.toDataURL();
            link.click();
            
            // 
            track.stop();
            
            setShareMessage('');
            setTimeout(() => setShareMessage(''), 3000);
          });
        }).catch(error => {
          console.error(':', error);
          setShareMessage('');
          setTimeout(() => setShareMessage(''), 3000);
        });
      } else {
        setShareMessage('');
        setTimeout(() => setShareMessage(''), 3000);
      }
    } catch (error) {
      console.error(':', error);
      setShareMessage('');
      setTimeout(() => setShareMessage(''), 3000);
    }
  };

  const copyLink = async () => {
    try {
      const designData = {
        name: savedDesign?.designName || '',
        beads: savedDesign?.beads || [],
        timestamp: savedDesign?.timestamp || new Date().toISOString(),
        scores: scores
      };
      
      const shareUrl = `${window.location.origin}${window.location.pathname}?design=${encodeURIComponent(JSON.stringify(designData))}`;
      
      await navigator.clipboard.writeText(shareUrl);
      setShareMessage('');
      setTimeout(() => setShareMessage(''), 3000);
    } catch (error) {
      console.error(':', error);
      setShareMessage('');
      setTimeout(() => setShareMessage(''), 3000);
    }
  };

  const shareToSocial = (platform) => {
    const designData = {
      name: savedDesign?.designName || '',
      beads: savedDesign?.beads || [],
      timestamp: savedDesign?.timestamp || new Date().toISOString(),
      scores: scores
    };
    
    const shareUrl = `${window.location.origin}${window.location.pathname}?design=${encodeURIComponent(JSON.stringify(designData))}`;
    const shareText = `${savedDesign?.designName || ''}`;
    
    let shareLink = '';
    
    switch (platform) {
      case 'facebook':
        shareLink = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(shareUrl)}&quote=${encodeURIComponent(shareText)}`;
        break;
      case 'twitter':
        shareLink = `https://twitter.com/intent/tweet?url=${encodeURIComponent(shareUrl)}&text=${encodeURIComponent(shareText)}`;
        break;
      case 'line':
        shareLink = `https://social-plugins.line.me/lineit/share?url=${encodeURIComponent(shareUrl)}&text=${encodeURIComponent(shareText)}`;
        break;
      case 'instagram':
        captureScreenshot();
        setShareMessage(' Instagram');
        setTimeout(() => setShareMessage(''), 5000);
        return;
      default:
        return;
    }
    
    window.open(shareLink, '_blank', 'width=600,height=400');
  };

  // 
  const getSafeScorePosition = (score, baseX, baseY) => {
    // 6-8
    // 6 = 7 = 8 = 
    const level = score - 6; // 0, 1, 2
    const maxRadius = 15; // 
    
    // 
    const x = baseX + (level * maxRadius);
    const y = baseY + (level * maxRadius);
    
    return { x, y };
  };

  // 
  const getHexagonScorePoints = (scores) => {
    // 
    const centerX = 175;
    const centerY = 175;
    
    // 0
    // 
    const points = [
      //  (y)
      { x: centerX, y: centerY - (scores.love / 10) * 100 },
      
      //  (30)
      { x: centerX + (scores.love / 10) * 86.6, y: centerY - (scores.love / 10) * 50 },
      
      //  (30)
      { x: centerX + (scores.windfall / 10) * 86.6, y: centerY + (scores.windfall / 10) * 50 },
      
      //  (y)
      { x: centerX, y: centerY + (scores.regularIncome / 10) * 100 },
      
      //  (30)
      { x: centerX - (scores.career / 10) * 86.6, y: centerY + (scores.career / 10) * 50 },
      
      //  (30)
      { x: centerX - (scores.health / 10) * 86.6, y: centerY - (scores.health / 10) * 50 }
    ];
    
    return points;
  };

  return (
    <>
      {/*  Loading  */}
      {isGlobalLoading && (
        <div className="global-loading-overlay">
          <div className="loading-content">
            <div className="psychic-loading-avatar">
              <img 
                src="/psychic-medium.jpeg" 
                alt="" 
                className="psychic-loading-image"
              />
            </div>
            <div className="loading-text">
              <p> ...</p>
              <p> ...</p>
            </div>
          </div>
        </div>
      )}
      
      <Fade in={!isExiting && !isGlobalLoading} timeout={800}>
        <div className="bead-rating-container bead-rating-page">
          {/*  */}
          <div className="rating-header">
            <h1>  </h1>
          </div>

          <div className="rating-content">
            {/*  */}
            <div className="left-panel">
              {/*  */}
              <div className="dialogue-box">
                {!isAnalyzing && !showResult && (
                  <div className="welcome-message">
                    <p>...</p>
                    {savedDesign ? (
                      <p></p>
                    ) : (
                      <p></p>
                    )}
                  </div>
                )}

                {isAnalyzing && (
                  <div className="analyzing-message">
                    <div className="crystal-animation"></div>
                    <p>...</p>
                    <p>...</p>
                  </div>
                )}

                {showResult && (
                  <div className="result-message">
                    <p className="advice-text">{currentMessage}</p>
                    {currentMessage.length < fullMessage.length && (
                      <div className="typing-indicator">|</div>
                    )}
                  </div>
                )}
                
                {/*  -  */}
                <div className="psychic-avatar-in-dialogue">
                  <img 
                    src="/psychic-medium.jpeg" 
                    alt="" 
                    className="psychic-image-full"
                  />
                  <div className="psychic-title-small">:</div>
                </div>
              </div>
            </div>

            {/*  */}
            <div className="right-panel">
              {/*  */}
              <div className="bead-design-section">
                <h3> </h3>
                {savedDesign ? (
                  <div className="design-display">
                    <div className="design-info">
                      <h4>{savedDesign.designName}</h4>
                      <p> {(() => {
                        //  0.5 
                        const bigBeads = savedDesign.beads.filter(bead => 
                          !(bead.type === '' || bead.type === '' || bead.type === '')
                        ).length;
                        const smallBeads = savedDesign.beads.filter(bead => 
                          bead.type === '' || bead.type === '' || bead.type === ''
                        ).length;
                        const actualTotal = bigBeads + (smallBeads * 0.5);
                        return `${actualTotal.toFixed(1)} `;
                      })()}</p>
                      <p>: {new Date(savedDesign.timestamp).toLocaleString()}</p>
                    </div>
                    
                    {/*  -  */}
                    <div className="bead-design-visualization">
                      <div className="bead-circle-container">
                        {/*  */}
                        {(() => {
                          const beadCount = savedDesign.beads.length;
                          let radius = 80; // 
                          let stringType = 'half'; // 
                          
                          // 
                          if (beadCount <= 12) {
                            radius = 80; // 13
                            stringType = 'half';
                          } else if (beadCount <= 15) {
                            radius = 100; // 13-154/3
                            stringType = 'four-thirds';
                          } else {
                            radius = 120; // 16
                            stringType = 'full';
                          }
                          
                          return (
                            <>
                              {/*  */}
                              <div 
                                className={`bead-string bead-string-${stringType}`}
                                style={{
                                  width: `${radius * 2}px`,
                                  height: `${radius * 2}px`,
                                  left: '50%',
                                  top: '50%',
                                  transform: 'translate(-50%, -50%)'
                                }}
                              ></div>
                              
                              {/*  */}
                              {savedDesign.beads.map((bead, index) => {
                                const totalBeads = savedDesign.beads.length;
                                const angle = (index / totalBeads) * 2 * Math.PI - Math.PI / 2;
                                const centerX = 150;
                                const centerY = 150;
                                
                                const x = centerX + radius * Math.cos(angle);
                                const y = centerY + radius * Math.sin(angle);
                                
                                return (
                                  <div 
                                    key={index}
                                    className={`design-bead-circle ${(bead.type === '' || bead.type === '' || bead.type === '') ? 'small-bead' : ''}`}
                                    style={{ 
                                      left: `${x}px`,
                                      top: `${y}px`,
                                      animationDelay: `${index * 0.1}s`,
                                      zIndex: index + 10
                                    }}
                                    title={`${bead.name} - ${bead.type}`}
                                  >
                                    <img 
                                      src={`/${bead.image}`}
                                      alt={bead.name}
                                      className="design-bead-image"
                                    />
                                  </div>
                                );
                              })}
                            </>
                          );
                        })()}
                      </div>
                    </div>
                    
                                         <div className="design-controls">
                       <button 
                         onClick={startAnalysis}
                         disabled={isAnalyzing}
                         className="design-button primary-button"
                       >
                         <div className="button-icon"></div>
                         <div className="button-text">{isAnalyzing ? '...' : ''}</div>
                       </button>
                       <button 
                         onClick={clearSavedDesign}
                         className="design-button secondary-button"
                       >
                         <div className="button-icon"></div>
                         <div className="button-text"></div>
                       </button>
                     </div>
                     
                     {/*  */}
                     <div className="share-controls">
                       {shareMessage && (
                         <div className="share-message">
                           {shareMessage}
                         </div>
                       )}
                       
                       <button 
                         onClick={() => setShowShareMenu(!showShareMenu)}
                         className="design-button share-button"
                       >
                         <div className="button-icon"></div>
                         <div className="button-text"></div>
                       </button>
                       
                       {showShareMenu && (
                         <div className="share-menu">
                           <button 
                             className="share-option"
                             onClick={captureScreenshot}
                             title=""
                           >
                              
                           </button>
                           
                           <button 
                             className="share-option"
                             onClick={copyLink}
                             title=""
                           >
                              
                           </button>
                           
                           <div className="social-share-buttons">
                             <button 
                               className="social-share-btn facebook"
                               onClick={() => shareToSocial('facebook')}
                               title=" Facebook"
                             >
                                Facebook
                             </button>
                             
                             <button 
                               className="social-share-btn twitter"
                               onClick={() => shareToSocial('twitter')}
                               title=" Twitter"
                             >
                                Twitter
                             </button>
                             
                             <button 
                               className="social-share-btn line"
                               onClick={() => shareToSocial('line')}
                               title=" Line"
                             >
                                Line
                             </button>
                             
                             <button 
                               className="social-share-btn instagram"
                               onClick={() => shareToSocial('instagram')}
                               title=" Instagram"
                             >
                                Instagram
                             </button>
                           </div>
                         </div>
                       )}
                     </div>
                  </div>
                ) : (
                  <div className="no-design-message">
                    <div className="no-design-icon"></div>
                    <h4></h4>
                    <p></p>
                    <Button 
                      variant="outlined" 
                      size="medium"
                      onClick={goToBeadCabinet}
                      className="border-blue-400 text-blue-400 hover:bg-blue-400/10"
                      startIcon={<span></span>}
                    >
                      
                    </Button>
                  </div>
                )}
              </div>

              {/*  */}
              <div className="radar-chart-section">
                <h3> </h3>
                
                <div className="radar-chart">
                  {/* SVG */}
                  <svg 
                    className="radar-svg" 
                    viewBox="0 0 350 350" 
                    width="350" 
                    height="350"
                  >
                    {/*  -  */}
                    <polygon 
                      points="175,75 275,125 275,225 175,275 75,225 75,125" 
                      fill="none" 
                      stroke="rgba(255,255,255,0.3)" 
                      strokeWidth="1"
                    />
                    <polygon 
                      points="175,105 245,145 245,205 175,245 105,205 105,145" 
                      fill="none" 
                      stroke="rgba(255,255,255,0.3)" 
                      strokeWidth="1"
                    />
                    <polygon 
                      points="175,135 215,165 215,185 175,215 135,185 135,165" 
                      fill="none" 
                      stroke="rgba(255,255,255,0.3)" 
                      strokeWidth="1"
                    />
                    
                    {/*  -  */}
                    <line x1="175" y1="175" x2="175" y2="75" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    <line x1="175" y1="175" x2="275" y2="125" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    <line x1="175" y1="175" x2="275" y2="225" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    <line x1="175" y1="175" x2="175" y2="275" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    <line x1="175" y1="175" x2="75" y2="225" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    <line x1="175" y1="175" x2="75" y2="125" stroke="rgba(255,255,255,0.5)" strokeWidth="1" />
                    
                    {/* AI -  */}
                    {showResult && (() => {
                      const scorePoints = getHexagonScorePoints(scores);
                      const pointsString = scorePoints.map(point => `${point.x},${point.y}`).join(' ');
                      
                      return (
                        <polygon 
                          key={`fill-${JSON.stringify(scores)}`}
                          points={pointsString}
                          fill="rgba(138, 43, 226, 0.6)" 
                          stroke="rgba(138, 43, 226, 0.8)" 
                          strokeWidth="2"
                        />
                      );
                    })()}
                    
                    {/*  */}
                    {showResult && (() => {
                      const scorePoints = getHexagonScorePoints(scores);
                      
                      return (
                        <>
                          {scorePoints.map((point, index) => (
                            <circle 
                              key={`point-${index}-${JSON.stringify(scores)}`}
                              cx={point.x} 
                              cy={point.y} 
                              r="4" 
                              fill="#ffd700" 
                            />
                          ))}
                        </>
                      );
                    })()}
                    
                                         {/*  -  */}
                     <text x="175" y="60" className="axis-label" textAnchor="middle"> ({scores.love})</text>
                     <text x="290" y="130" className="axis-label" textAnchor="start"> ({scores.love})</text>
                     <text x="290" y="230" className="axis-label" textAnchor="start"> ({scores.windfall})</text>
                     <text x="175" y="300" className="axis-label" textAnchor="middle"> ({scores.regularIncome})</text>
                     <text x="60" y="230" className="axis-label" textAnchor="end"> ({scores.career})</text>
                     <text x="60" y="130" className="axis-label" textAnchor="end"> ({scores.health})</text>
                  </svg>
                </div>
              </div>
            </div>
          </div>

          {/*  */}
          <div className="bottom-navigation">
            <div className="nav-grid">
              <button className="nav-button" onClick={goHome}>
                <div className="nav-icon"></div>
                <div className="nav-text"></div>
              </button>
              <button className="nav-button" onClick={goToBeadCabinet}>
                <div className="nav-icon"></div>
                <div className="nav-text"></div>
              </button>
              <button className="nav-button" onClick={() => window.location.href = '/guide'}>
                <div className="nav-icon"></div>
                <div className="nav-text"></div>
              </button>
            </div>
          </div>
        </div>
      </Fade>
    </>
  );
};

export default BeadRating;
