// 
const StoreCard = ({ store, type }) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [isLiked, setIsLiked] = useState(false);
  const cardRef = useRef(null);

  // 
  useEffect(() => {
    const card = cardRef.current;
    if (!card) return;

    let startY = 0;
    let startTime = 0;

    const handleTouchStart = (e) => {
      startY = e.touches[0].clientY;
      startTime = Date.now();
    };

    const handleTouchEnd = (e) => {
      const endY = e.changedTouches[0].clientY;
      const endTime = Date.now();
      const deltaY = Math.abs(endY - startY);
      const deltaTime = endTime - startTime;

      // /
      if (deltaY < 10 && deltaTime < 200) {
        setIsExpanded(!isExpanded);
      }
    };

    card.addEventListener('touchstart', handleTouchStart);
    card.addEventListener('touchend', handleTouchEnd);

    return () => {
      card.removeEventListener('touchstart', handleTouchStart);
      card.removeEventListener('touchend', handleTouchEnd);
    };
  }, [isExpanded]);

  const renderStars = (rating) => {
    const stars = [];
    for (let i = 1; i <= 5; i++) {
      stars.push(
        <span key={i} className={`star ${i <= rating ? 'filled' : ''}`}>
          
        </span>
      );
    }
    return stars;
  };

  const handleLike = (e) => {
    e.stopPropagation();
    setIsLiked(!isLiked);
  };

  const handleContact = (e) => {
    e.stopPropagation();
    if (store.phone) {
      window.open(`tel:${store.phone}`);
    }
  };

  return (
    <div className={`store-card ${type}`} ref={cardRef}>
      <div className="store-header">
        <h3 className="store-name">{store.name}</h3>
        <div className="store-rating">
          {renderStars(store.rating)}
          <span className="rating-number">{store.rating}</span>
        </div>
      </div>
      
      <div className="store-info">
        {store.location && (
          <div className="info-item">
            <span className="icon"></span>
            <span>{store.location}</span>
          </div>
        )}
        {store.phone && (
          <div className="info-item">
            <span className="icon"></span>
            <span>{store.phone}</span>
          </div>
        )}
        {store.website && (
          <div className="info-item">
            <span className="icon"></span>
            <span>{store.website}</span>
          </div>
        )}
        <div className="info-item">
          <span className="icon"></span>
          <span>{store.hours}</span>
        </div>
        <div className="info-item">
          <span className="icon"></span>
          <span>{store.price}</span>
        </div>
        <div className="info-item">
          <span className="icon"></span>
          <span>{store.type}</span>
        </div>
      </div>

      <div className="store-features">
        <h4></h4>
        <div className="feature-tags">
          {store.features.map((feature, index) => (
            <span key={index} className="feature-tag">{feature}</span>
          ))}
        </div>
      </div>

      <div className="store-reasons">
        <h4></h4>
        <div className="reason-tags">
          {store.reasons.map((reason, index) => (
            <span key={index} className="reason-tag">{reason}</span>
          ))}
        </div>
      </div>

      <div className="store-description">
        <p>{store.description}</p>
      </div>

      <div className="store-actions">
        <button 
          className="action-btn primary"
          onClick={() => setIsExpanded(!isExpanded)}
        >
          {isExpanded ? '' : ''}
        </button>
        <button 
          className={`action-btn secondary ${isLiked ? 'liked' : ''}`}
          onClick={handleLike}
        >
          {isLiked ? '' : ''} {isLiked ? '' : ''}
        </button>
        {store.phone && (
          <button className="action-btn secondary" onClick={handleContact}>
             
          </button>
        )}
      </div>

      {isExpanded && (
        <div className="store-details">
          <div className="store-images">
            <h4></h4>
            <div className="image-gallery">
              {store.images.map((image, index) => (
                <img 
                  key={index} 
                  src={image} 
                  alt={`${store.name}  ${index + 1}`}
                  className="store-image"
                  onError={(e) => {
                    e.target.style.display = 'none';
                    e.target.nextSibling.style.display = 'flex';
                  }}
                />
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

// 
const SectionHeader = ({ title, icon, description }) => (
  <div className="section-header">
    <div className="section-icon">{icon}</div>
    <div className="section-content">
      <h2>{title}</h2>
      <p>{description}</p>
    </div>
  </div>
);

// 
const BeadStores = () => {
  const [activeSection, setActiveSection] = useState('all');
  const [isMobile, setIsMobile] = useState(false);
  const [showScrollTop, setShowScrollTop] = useState(false);
  const containerRef = useRef(null);

  // 
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  // 
  useEffect(() => {
    const handleScroll = () => {
      setShowScrollTop(window.scrollY > 300);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const sections = [
    { id: 'all', name: '', icon: '' },
    { id: 'special', name: '', icon: '' },
    { id: 'online', name: '', icon: '' },
    { id: 'offline', name: '', icon: '' }
  ];

  const getFilteredStores = () => {
    switch (activeSection) {
      case 'special':
        return storeData.specialStores;
      case 'online':
        return storeData.onlineStores;
      case 'offline':
        return storeData.offlineStores;
      default:
        return [...storeData.specialStores, ...storeData.onlineStores, ...storeData.offlineStores];
    }
  };

  return (
    <div className="bead-stores-container" ref={containerRef}>
      {/*  */}
      <div className="page-header">
        <h1> </h1>
        <p></p>
      </div>

      {/*  */}
      <div className="navigation-tabs">
        {sections.map(section => (
          <button
            key={section.id}
            className={`nav-tab ${activeSection === section.id ? 'active' : ''}`}
            onClick={() => setActiveSection(section.id)}
          >
            <span className="tab-icon">{section.icon}</span>
            {!isMobile && <span className="tab-name">{section.name}</span>}
          </button>
        ))}
      </div>

      {/*  */}
      <div className="store-stats">
        <StatCard 
          label=""
          value={getFilteredStores().length}
          variant="number"
        />
        <StatCard 
          label=""
          value="4.7"
          variant="number"
        />
        <StatCard 
          label=""
          value="24"
          variant="number"
        />
      </div>

      {/*  */}
      <div className="stores-content">
        {activeSection === 'all' && (
          <>
            {/*  */}
            <div className="store-section">
              <SectionHeader
                title=" "
                icon=""
                description=""
              />
              <div className="stores-grid">
                {storeData.specialStores.map(store => (
                  <StoreCard key={store.id} store={store} type="special" />
                ))}
              </div>
            </div>

            {/*  */}
            <div className="store-section">
              <SectionHeader
                title=" "
                icon=""
                description=""
              />
              <div className="stores-grid">
                {storeData.onlineStores.map(store => (
                  <StoreCard key={store.id} store={store} type="online" />
                ))}
              </div>
            </div>

            {/*  */}
            <div className="store-section">
              <SectionHeader
                title=" "
                icon=""
                description=""
              />
              <div className="stores-grid">
                {storeData.offlineStores.map(store => (
                  <StoreCard key={store.id} store={store} type="offline" />
                ))}
              </div>
            </div>
          </>
        )}

        {activeSection !== 'all' && (
          <div className="stores-grid">
            {getFilteredStores().map(store => (
              <StoreCard key={store.id} store={store} type={activeSection} />
            ))}
          </div>
        )}
      </div>

      {/*  */}
      <div className="page-footer">
        <div className="footer-content">
          <h3> </h3>
          <div className="footer-reasons">
            <div className="reason-item">
              <span className="reason-icon"></span>
              <span></span>
            </div>
            <div className="reason-item">
              <span className="reason-icon"></span>
              <span></span>
            </div>
            <div className="reason-item">
              <span className="reason-icon"></span>
              <span></span>
            </div>
            <div className="reason-item">
              <span className="reason-icon"></span>
              <span></span>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      {showScrollTop && (
        <button 
          className="scroll-top-btn"
          onClick={scrollToTop}
          aria-label=""
        >
          
        </button>
      )}
    </div>
  );
};

export default BeadStores;
